:root {
  --fon-accent: #0eba11;
  --white-color: #f6f5f5;
}

* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  background-color: var(--white-color);
}

img {
  width: 100%;
  height: 300px;
  border-radius: 10px;
  margin-bottom: 10px;
  display: block;
}

.search-form {
  background-color: var(--fon-accent);
  width: 100%;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 10px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

input {
  min-width: 500px;
  border-color: var(--fon-accent);
  border-radius: 10px;
  margin-right: 20px;
  padding: 10px;
  font-size: 20px;
}

.gallery {
  flex-wrap: wrap;
  align-items: center;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  display: flex;
}

.photo-card {
  min-width: 360px;
  border-radius: 10px;
  flex-basis: calc(25% - 30px);
  margin-top: 20px;
  margin-left: 20px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.info {
  list-style: none;
}

.load-more {
  background-color: var(--fon-accent);
  min-width: 60px;
  color: var(--white-color);
  cursor: pointer;
  border-radius: 10px;
  margin: 0 auto;
  padding: 15px;
  font-size: 16px;
  display: block;
}

.form-button {
  background-color: var(--white-color);
  cursor: pointer;
  border: none;
  border-color: var(--fon-accent);
  border-radius: 10px;
  margin-left: 0;
  padding: 10px;
  font-size: 20px;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.info {
  text-align: center;
  color: var(--white-color);
  justify-content: center;
  gap: 10px;
  display: flex;
}

.info-item {
  background-color: var(--fon-accent);
  border-radius: 10px;
}

.is-hidden {
  display: none;
}

